<template>
  <div class="maptools-view">
    <!-- 为每个按钮加一个值用于判断是否被点击过，如点击过则再次点击无效 -->
    <span class="maptools-item" @click="pointId ? viewPoint() : 0"
      >显示所有网点</span
    >
    <span class="maptools-item" @click="taisenId ? viewTaisen() : 0"
      >泰森多边形</span
    >
    <span class="maptools-item" @click="bufferId ? viewBuffer() : 0"
      >查看缓冲区</span
    >
    <span class="maptools-item" @click="clearMap">清理屏幕</span>
  </div>
</template>

<script>
import { loadModules } from "esri-loader";
//引入网点
import pois from "../assets/poi";
const options = {
  //引入ArcGIS API
  url: "https://js.arcgis.com/4.24/init.js",
  css: "https://js.arcgis.com/4.24/esri/themes/light/main.css",
};
export default {
  name: "MapTools",
  data() {
    return {
      pointId: true,
      taisenId: true,
      bufferId: true,
    };
  },
  methods: {
    //显示poi兴趣点功能
    async viewPoint() {
      //给map赋值
      const map = this.$store.getters._getDefaultMapView.map;
      //调用api
      const [Graphic, GraphicsLayer] = await loadModules(
        ["esri/Graphic", "esri/layers/GraphicsLayer"],
        options
      );
      //实例化一个图层
      const graphicsLayer = new GraphicsLayer();
      //给map添加图层
      map.add(graphicsLayer);
      //循环遍历poi.js里面的poi兴趣点
      pois.pois.forEach(function (e) {
        //创建poi兴趣点
        const point = {
          type: "point",
          longitude: e.WGS84_E,
          latitude: e.WGS84_N,
        };
        //自定义点样式
        const simpleMarkerSymbol = {
          type: "simple-marker",
          color: [220, 20, 60, 0.7],
        };
        //创建弹窗
        const popupTemplate = {
          title: "{Name}",
          content: "{Description}",
        };
        //给弹窗元素赋值
        const attributes = {
          Name: e.Name,
          Description: "地址:" + e.Address,
        };
        //实例化poi兴趣点图层
        const pointGraphic = new Graphic({
          geometry: point,
          symbol: simpleMarkerSymbol,
          attributes: attributes,
          popupTemplate: popupTemplate,
        });
        //为图层添加poi兴趣点图层
        graphicsLayer.add(pointGraphic);
        //循环结尾
      });
      this.graphicsLayer = graphicsLayer;
      //将此按钮设置为不可点击
      this.pointId = false;
    },

    //查看泰森多边形
    async viewTaisen() {
      //给map赋值
      const map = this.$store.getters._getDefaultMapView.map;
      //调用api
      const [FeatureLayer] = await loadModules(
        ["esri/layers/FeatureLayer"],
        options
      );
      //实例化图层
      const parksLayerd = new FeatureLayer({
        url: "https://services.arcgis.com/tb3a6kOqLcSGNMYu/arcgis/rest/services/taisen/FeatureServer",
      });
      this.parksLayerdA = parksLayerd;

      map.add(parksLayerd, 0);
      //将此按钮设置为不可点击
      this.taisenId = false;
    },

    //查看缓冲区
    async viewBuffer() {
      //给map赋值
      const map = this.$store.getters._getDefaultMapView.map;
      //调用api
      const [FeatureLayer] = await loadModules(
        ["esri/layers/FeatureLayer"],
        options
      );
      const parksLayerd = new FeatureLayer({
        url: "https://services.arcgis.com/tb3a6kOqLcSGNMYu/arcgis/rest/services/Buffer/FeatureServer",
      });
      this.parksLayerdB = parksLayerd;

      map.add(parksLayerd, 0);
      //将此按钮设置为不可点击
      this.bufferId = false;
    },

    //清屏功能
    async clearMap() {
      const map = this.$store.getters._getDefaultMapView.map;
      // map.remove(this.graphicsLayer);
      // map.remove(this.parksLayerdA);
      // map.remove(this.parksLayerdB);
      map.removeAll();
      //恢复所有按钮的点击事件
      this.pointId = true;
      this.taisenId = true;
      this.bufferId = true;
    },
  },
};
</script>

<style>
.maptools-view {
  position: absolute;
  padding: 0 15px;
  height: 60px;
  top: 0px;
  right: 15px;
  background-color: #303133;
  color: white;
}
.maptools-item {
  display: inline-block;
  width: 150px;
  height: 60px;
  line-height: 60px;
  margin-left: 15px;
  cursor: pointer;
  color: wheat;
  text-align: center;
  background-color: #303133;
}

.maptools-item:first-child {
  margin-left: 0;
}
/*poi弹窗样式*/
.esri-view-width-xlarge .esri-popup__main-container {
  width: 300px;
  height: 180px;
  background-color: darkgray;
}
/*poi弹窗字体样式*/
div.esri-feature-content {
  font-size: 24px;
}
/*poi弹窗address部分样式*/
.esri-feature__main-container {
  line-height: normal;
  background-color: darkgray;
}
</style>