<template>
  <div id="mapview"></div>
</template>

<script>
//引入loadModules

import { loadModules } from "esri-loader";
import pois from "../../assets/poi";
const options = {
  //引入ArcGIS API
  url: "https://js.arcgis.com/4.24/init.js",
  css: "https://js.arcgis.com/4.24/esri/themes/light/main.css",
};
export default {
  name: "MapView",
  components: {},
  //mounted 生命周期函数，在MapView组件创建完成之后就会执行里面的函数
  mounted: function () {
    this._createMapView();
  },

  methods: {

    //async await是成对出现的，意思是把那些模块加载完成之后才可以执行后面的代码
    async _createMapView() {
      const [Map, MapView] = await loadModules(
        ["esri/Map", "esri/views/MapView", "esri/Basemap"],
        options
      );
      const map = new Map({
        basemap: "osm",
      });
      const view = new MapView({
        container: "mapview",
        center: [115.870334, 28.703776],
        map: map,
        zoom: 12,
      });
      this.$store.commit("_setDefaultMapView", view);

      const [Locate] = await loadModules(["esri/widgets/Locate"], options);
      //定位
      const locate = new Locate({
        view: view,
        useHeadingEnabled: false,
        goToOverride: function (view, options) {
          options.target.scale = 1500;
          this.longitude = options.target.target.longitude;
          this.latitude = options.target.target.latitude;
          this.viewSurround();
          return view.goTo(options.target);
        }.bind(this)//改变this指向使其指向vc,
      });
      view.ui.add(locate, "top-right");
    },

    async viewSurround() {

      const map = this.$store.getters._getDefaultMapView.map;
      //调用api
      const [Graphic, GraphicsLayer] = await loadModules(
        ["esri/Graphic", "esri/layers/GraphicsLayer"],
        options
      );
      //实例化一个图层
      const graphicsLayer = new GraphicsLayer();
      //给map添加图层
      map.add(graphicsLayer);

      function radians(degrees){
        return degrees * (Math.PI/180);
      }
      const jingduA = this.longitude;
      const weiduA = this.latitude;

      //定义地球半径，PI
      const R = 6371.393;
      const PI = Math.PI;

      //循环比较坐标得出距离
      pois.pois.forEach(function (e) {
        const jingduB = e.WGS84_E;
        const weiduB = e.WGS84_N;

        const a = Math.pow(Math.sin(radians(weiduA / 2 - weiduB / 2)), 2);
        const b =
          Math.cos((weiduA * PI) / 180) *
          Math.cos((weiduB * PI) / 180) *
          Math.pow(Math.sin(((jingduA / 2 - jingduB / 2) * PI) / 180), 2);

        const L = 2 * R * Math.asin(Math.pow(a + b, 0.5));

        if(L<2){
          const point = {
          type: "point",
          longitude: e.WGS84_E,
          latitude: e.WGS84_N,
        };
        //自定义点样式
        const simpleMarkerSymbol = {
          type: "simple-marker",
          color: [220,20,60,0.7],
        };
        //创建弹窗
        const popupTemplate = {
          title: "{Name}",
          content: "{Description}",
        };
        //给弹窗元素赋值
        const attributes = {
          Name: e.Name,
          Description: "地址:" + e.Address,
        };
        //实例化poi兴趣点图层
        const pointGraphic = new Graphic({
          geometry: point,
          symbol: simpleMarkerSymbol,
          attributes: attributes,
          popupTemplate: popupTemplate,
        });
        //为图层添加poi兴趣点图层
        graphicsLayer.add(pointGraphic);
        }
      });
    },
  },
};
</script>

<style>
#mapview {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>